import React from 'react';
import axios from 'axios';
import {ReactComponent as Upload} from './svg/interface.svg'
import './App.css';

const imgs = [
  1,2,3,4,5,6,7,8,9,0,54,6,785,23,23,5,46,33,23,13,24,46,576,42,3,45,3645,7,68,7,98,908,0,2,46,576,57,68,686,769,80,8,3,45,3254,645,76,786,7,45,2,6,5,765,645,6,346,5,5,3,31,21,32,34,46,45675,
  1,2,3,4,5,6,7,8,9,0,54,6,785,23,23,5,46,33,23,13,24,46,576,42,3,45,3645,7,68,7,98,908,0,2,46,576,57,68,686,769,80,8,3,45,3254,645,76,786,7,45,2,6,5,765,645,6,346,5,5,3,31,21,32,34,46,45675  
]

const UploaDiv = (props) =>{
  return(
    <div className="upload">
      <input id="upload_img" type="file" onChange={props.upload} />
      <label htmlFor="upload_img"><Upload /></label>
    </div>
  )
}

const SearchResults = (props) =>{
  return(
    <div className="search-results">
      <div className="search">
        <div className="query">
          <span className="close" onClick={props.newsearch} >x</span>
          <img src={props.src} />
        </div>
      </div>
      <div className="images">
        {
          props.images.map((ig,i)=>{
            return (
              <div className="tile" key={i}>
                  <img src={ig.path} alt={ig.name} />
              </div>
            )
          })
        }
      </div>
    </div>
  )
}

function App() {
  let [container,containerState] = React.useState({
    cont:<UploaDiv upload={upload} />
  })

  let [search_image,search_imageState] = React.useState({
    image:null
  })

  function newsearch(){
    containerState({
      cont:<UploaDiv upload={upload} />
    })
  }

  async function upload(e){
    let file = e.target.files[0];
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = async function(e){
      containerState({
        cont:<SearchResults  src={e.target.result} images={[]} />,
      })

      let form =  new FormData();
      form.append("image",file)
      axios({
        method:"POST",
        url:"http://localhost:8080/search",
        data:form
      }).then(response=>{
        containerState({
          ...container,
          cont:<SearchResults  src={e.target.result} images={response.data.results} newsearch={newsearch} />,
        })
      })
    }
  }
  
  return (
    <div className="main">
      <div className="title">
        Image Search For MNIST Dataset
      </div>
      {container.cont}
    </div>
  );
}

export default App;
